.container{
    padding: 50px 13% 200px 13%;
}

.analiticCard_way{
    color: #333;
}

.card_content{
    margin-top: 20px;
    display: flex;
    width: 100%;
    justify-content: space-between;
    position: relative;
}

.card_img{
    position: sticky;
    top: 150px;
    width: 45%px;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.card_img img{
    width: 100%;
    filter: drop-shadow(0px 4px 12px rgba(0, 0, 0, 0.15));
}

.shareIt{
    width: 100%;
    height: 160px;
    background-color: #fff;
    margin-top: 30px;
    padding: 30px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
}

.shareIt span{
    font-weight: 700;
    font-size: 18px;
}

.shareIt_icons{
    margin-top: 25px;
}

.card_description{
    width: 48%;
    background-color: #fff;
    padding: 30px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
    height: auto;
}

.card_description span{
    color: var(--green);
}

.card_description h4{
    font-size: 24px;
    margin-top: 10px;
    margin-bottom: 15px;
}

.card_description p{
    text-align: justify;
    line-height: 17px;
    font-size: 14px;
}

.research_info{
    width: 100%;
    background-color: #fff;
    margin-top: 30px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
    padding: 20px 30px;
}

.research_info_header{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.research_info_header span:nth-child(1){
    color: #1B8E3D;
}

.research_info_line{
    background-color: #BDBDBD;
    width: 100%;
    height: 4px;
    margin-top: 20px;
    margin-bottom: 10px;
}

.research_info_content{
    display: flex;
}

.research_info_content h5{
    width: 70%;
}

.research_info_details{
    display: flex;
    flex-direction: column;
}

.research_info_hashtag{
    margin-top: 20px;
}

.research_info_hashtag span{
    background-color: #BDBDBD;
    color: #fff;
    padding: 4px 8px;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
}

.research_info_hashtag span + span{
    margin-left: 10px;
}